// Narrative styling
// ==================
.narrative-component {

  .narrative-content-title {
    .item-title;
    font-size      : 18px !important;
    font-family    : @font-family-headings;
    margin-bottom  : @title-text-margin;
    border-bottom  : @global-border;
    padding-bottom : 4px;
  }
  .narrative-content {
    border        : @global-border;
    border-radius : 2px;
    background    : @item-color;
    background    : linear-gradient(165deg, @white, darken(@component-background-color, 5%) 75%);
    color         : @item-text-color;
  }

  .narrative-controls {
    width               : (@icon-size * 2.5);
    height              : (@icon-size * 2.5);
    margin-top          : -(@icon-size * 2);
    background          : @button-color;
    background          : linear-gradient(165deg, @button-color, lighten(@button-color, 15%) 75%);
    background-size     : 200% 200%;
    background-position : 100% 0;
    color               : @button-text-color;
    border-radius       : 2px;
    transition          : all .6s cubic-bezier(.25, .8, .25, 1) !important;
    box-shadow          : 0 1px 2px rgba(0, 0, 0, 0.06), 0 1px 1px rgba(0, 0, 0, 0.12);

    .icon {
      color : @button-text-color;
    }

    &:hover {
      background-color    : transparent !important;
      background-position : 0 0 !important;
      color               : @button-text-color-hover;
      box-shadow          : 0 0 0 rgba(0, 0, 0, 0.06), 0 0 0 rgba(0, 0, 0, 0.12);

      .icon {
        color : @button-text-color-hover !important;
      }
    }
  }

  .narrative-text-controls .narrative-content .narrative-content-inner {

    .narrative-controls-container {
      top : 0;

      .narrative-controls {
        width      : auto;
        height     : auto;
        margin-top : (@icon-size * 1.5);

        .icon {
          font-size : @icon-size;
        }
      }

      .narrative-indicators {
        margin-top : (@icon-size/2);
        text-align : center;
        width      : 100%;
        left       : 0;

        .narrative-progress {
          display          : inline-block;
          background-color : darken(@item-color, 10%);

          &.selected {
            background-color : @item-color;
          }
        }

      }
    }

    .narrative-content-item {
      padding-top : (@icon-size * 2 + 16); // indicator height + controlls
    }
  }

  .narrative-progress {
    background-color : darken(@component-background-color, 10%);
    border           : inherit;

    &.selected {
      background-color : lignten(@item-color, 10%);
    }
  }

  .narrative-strapline-title {
    background          : @item-color;
    color               : @item-text-color;
    border              : none !important;
    background          : linear-gradient(165deg, @button-color, lighten(@button-color, 15%) 75%);
    background-size     : 200% 200%;
    background-position : 50% 0;
    .item-title;

    .icon {
      padding : @item-padding-top @item-padding-right @item-padding-bottom;
      color   : @item-text-color;
    }

    &:hover {
      background-position : 0 0;
      color               : @item-text-color;

      .icon {
        color : @item-text-color;
      }

    }

    &:disabled {
      background          : linear-gradient(165deg, @button-color, lighten(@button-color, 15%) 75%) !important;
      background-size     : 200% 200% !important;
      background-position : 50% 0 !important;
    }
  }

  .narrative-strapline-title-inner {
    padding-left  : (@item-padding-left);
    padding-right : (@item-padding-left + @item-padding-right + @icon-size);

    .dir-rtl & {
      padding-right : (@item-padding-left);
      padding-left  : (@item-padding-left + @item-padding-right + @icon-size);
    }
  }
}

.narrative-component .narrative-controls {
  &.disabled,
  &:disabled,
  &[aria-disabled="true"] {
    visibility : hidden;
  }
  &:hover {
    background-position : 0 0;
    color               : @item-text-color;

    .icon {
      color : @item-text-color;
    }
  }
}