.menu.frame-menu .menu-item .menu-item-container > .menu-item-rect .menu-item-inner {
  background-color: @primary-color;
}

.menu.frame-menu .menu-item .page-level-progress-menu-item > .page-level-progress-menu-item-indicator{
  background-color: rgba(252, 252, 252, 0.6);
  border-top-color: rgba(252, 252, 252, 0.8);
}

.menu.frame-menu .menu-item .page-level-progress-menu-item > .page-level-progress-menu-item-indicator .page-level-progress-menu-item-indicator-bar{
  background-color: rgba(49,117,165, .4);
}

.menu.frame-menu .menu-item .menu-item-container > .menu-item-rect .menu-item-title .complete-icon {
  color : lighten(@validation-success, 20%);
}

.menu.frame-menu .menu-item .page-level-progress-menu-item .percent-label {
  color : #000;
}
.menu.frame-menu .menu-item .menu-item-container > .menu-item-rect .menu-item-button button{
  background          : linear-gradient(165deg, @button-color, lighten(@button-color, 2%) 75%);
}