.order-items-list {
  list-style : none;
  padding    : 4px;
  margin     : 0;

  li {
    color               : @item-text-color;
    background          : @item-color;
    background          : @secondary-gradient;
    background-size     : 300% 300%;
    background-position : 90% 0;
    border-radius       : 2px;
    transition          : all .6s cubic-bezier(.25, .8, .25, 1);
    border              : @global-border;
    margin              : .25rem;
    user-select         : none;
    .item-title;

    &:hover {
      background-position : 20% 0;
    }
  }
}

.gu-mirror,
.order-items-list > li {
  position         : relative;
  box-sizing       : border-box;
  list-style       : none;
  padding          : 16px;
  background-color : @item-color;
  border           : 1px solid @item-color;
  cursor           : move;

  &[aria-disabled="true"],
  &[aria-disabled="true"]:hover {
    border : 1px solid @item-color !important;
  }

  &:before {
    position               : absolute;
    right                  : @component-padding / 2;
    top                    : 50%;
    margin-top             : -12px;
    display                : none;
    width                  : 24px;
    height                 : 24px;
    font-size              : 24px;
    text-decoration        : none;
    text-align             : center;
    font-family            : 'vanilla';
    speak                  : none;
    font-style             : normal;
    font-weight            : normal;
    font-variant           : normal;
    text-transform         : none;
    line-height            : 1;
    -webkit-font-smoothing : antialiased;
  }

  &.incorrect:before {
    display : block !important;
    content : "\e634";
    color   : @validation-error;
  }

  &.correct:before {
    display : block !important;
    content : "\e633";
    color   : @validation-success;
  }
}
