.parallax-inner{
  .article-parallax-wrap {
    width: 100%;
  }
}

.parallax-bg-background {
  background : radial-gradient(circle at bottom, #fff, darken(@component-background-color, 5%) 75%);
}

.parallax-bg-primary {
  background : radial-gradient(circle at bottom, @secondary-color, @menu-header-background 50%);
}

.parallax-bg-secondary {
  background : radial-gradient(circle at bottom, lighten(@secondary-color, 15%), @secondary-color 60%);
}

.parallax-bg-tertiary {
  background : radial-gradient(circle at top right, lighten(@tertiary-color, 25%), @tertiary-color 95%);
}

.parallax-bg-quaternary {
  background : radial-gradient(circle at bottom left, @quaternary-color, darken(@quaternary-color, 15%) 75%);
}

.parallax-bg-foreground {
  background : linear-gradient(165deg, @foreground-color, lighten(@foreground-color, 25%) 75%);
}
