// Additional theme classes or attributes to be applied in JSON for more custom style 
// ====================================================================

// Divider block - used to separate content or break up pages. 
// =============

// Class applies alternative background color and component styling

.divider-block {
    background-color: @background-color-inverted;
    color: @foreground-color-inverted;
    text-align: center;
    .block-title,
    .block-body,
    .block-instruction,
    .component-title,
    .component-body,
    .component-instruction {
    	color: @foreground-color-inverted;
    	text-align: center;
    }
    .component-inner {
    	background-color: transparent;
    }
}

// Block background color - used to apply variable based background colors on blocks
// ======================

.block {
	&.primary-color {
		background-color: @primary-color;
	}

	&.secondary-color {
		background-color: @secondary-color;
	}

	&.tertiary-color {
		background-color: @tertiary-color;
	}

	&.background-color {
		background-color: @background-color;
	}

	&.background-color-inverted {
		background-color: @background-color-inverted;
		color: @font-color-inverted;
		.component-inner {
			background-color: transparent;
		}
	}
}

// Component background - used to apply variable based background color and padding on components
// ====================

.component-background {
	background-color: @component-background-color;
	padding: @component-padding;
}

// Assessment - remove graphic components for mobile view (when components snap down to single span).
// ==========

.assessment {
	.graphic-component {
		display: block;

		@media all and (max-width: @device-width-medium) {
			display: none;
		}
	}
}

// THIS CLASS IS FOR THE PURPOSE OF THE ADAPT DEMO 
// blank component demo - applied at block level to add a background image in place of a blank component
// ====================

.blank-background {
	background-image: url(assets/blank-bg.jpg);
	background-repeat: no-repeat;
	background-position: center;

	@media all and (max-width: @device-width-large) {
		background-image: url(assets/blank-bg-900.jpg);
		background-position: right;
	}

	@media all and (max-width: @device-width-medium) {
		background-image: none;
	}
}

.sr-only {
	position    : absolute;
	width       : 1px;
	height      : 1px;
	padding     : 0;
	overflow    : hidden;
	clip        : rect(0, 0, 0, 0);
	white-space : nowrap;
	clip-path   : inset(50%);
	border      : 0;
}


@table-border-color : #dee2e6;
@table-border-width : 1px;
@table-accent-bg : rgba(0, 0, 0, .05);

@table-head-bg : #e9ecef;
@table-head-color : @tertiary-color;

table {
	width            : 100%;
	margin-bottom    : 16px;
	color            : @black;
	background-color : @white; // Reset for nesting within parents with `background-color`.
	border           : @table-border-width solid @table-border-color;

	th,
	td {
		padding        : 12px;
		vertical-align : top;
		border         : @table-border-width solid @table-border-color;
	}

	thead {
		th,
		td {
			border-bottom-width : 2 * @table-border-width;
		}

		th {
			vertical-align : bottom;
			border-bottom  : (2 * @table-border-width) solid @table-border-color;
		}
	}
	tbody tr:nth-of-type(odd) {
		background-color : @table-accent-bg;
	}

	tbody + tbody {
		border-top : (2 * @table-border-width) solid @table-border-color;
	}
}

ul {
	list-style    : outside disc none;
	margin-left   : 14px;
	margin-bottom : 14px;
}

ol {
	list-style    : outside decimal none;
	margin-left   : 14px;
	margin-bottom : 14px;
}

dl {
	margin-top    : 0;
	margin-bottom : 16px;

	dt {
		font-weight : 700;
	}

	dd {
		margin-bottom : 8px;
		margin-left   : 0;
	}
}

blockquote {
	display          : block;
	font-size        : @item-title-font-size;
	margin           : 0 0 1rem;
	padding          : @component-padding;
	background-color : lighten(@primary-color, 40%);
	line-height      : 1.3;

	footer,
	cite {
		display    : block;
		font-size  : @body-text-font-size;
		font-style : italic;
		text-align : right;
		color      : lighten(@black, 15%);
	}

	cite:before {
		content : '\2014';
		display : inline-block;
		padding : 8px 4px 0 0;
	}
}