.tab-control {
  position         : relative;
  background-color : fade(@primary-color, 5%);

	.tab-panel__title {
		display        : -webkit-box;
		display        : flex;
		-webkit-box-orient : horizontal;
		-webkit-box-direction : normal;
		flex-direction : row;
		flex-wrap      : nowrap;
	}

  .tab-panel__heading {
	.item-title;
	  -webkit-box-flex   : 1;
	  flex-grow   : 1;
  }

  .tab-panel__body {
	padding : @item-padding;

	*:first-child {
	  margin-top : 0;
	}
  }

  &.tabs {
	  .tab-list {
		  display          : -webkit-box;
		  display          : flex;
		  -webkit-box-orient   : horizontal;
		  -webkit-box-direction   : normal;
		  flex-direction   : row;
		  flex-wrap        : nowrap;
		  -webkit-box-pack  : center;
		  justify-content  : center;
		  -webkit-box-align      : stretch;
		  align-items      : stretch;
		  background-color : fade(@primary-color, 5%);
		  list-style-type  : none;
		  margin           : 0;
		  padding          : 0;
	  }
	.tab {
		position   : relative;
		display    : block;
		-webkit-box-flex  : 1;
		flex-grow  : 1;
		text-align : center;
		padding    : 24px 16px;
		margin     : 0;
		cursor     : pointer;
		-webkit-transition : all 0.3s cubic-bezier(.25, .8, .25, 1);
		transition : all 0.3s cubic-bezier(.25, .8, .25, 1);
	  .item-title;

	  .tab__icon {
		font-size     : @item-title-font-size * 1.75;
		margin-bottom : 8px;
		opacity       : .6;
	  }

	  &:focus,
	  &:hover {
		background-color : fade(@primary-color, 10%);
	  }

	  &.visited {
		background-color : fade(@primary-color, 5%);
	  }

		&:after {
			content        : "";
			position       : absolute;
			top            : 100%;
			left           : 50%;
			margin-left    : -10px;
			width          : 0;
			height         : 0;
			border         : solid transparent;
			border-width   : 10px;
			pointer-events : none;
		}

	  &[aria-selected="true"] {
		background-color : fade(@white, 95%);
		&:after {
		  border-top-color : #fff;
		}
	  }
	}
	.tab-panel {

	  &.hidden {
		display : none;
	  }
	}
	.tab-panel__title {
	  padding : @item-padding @item-padding 0 @item-padding;
	}
	.tab-panel__heading__icon {
	  display : none;
	}
	.tab-panel__body > *:first-child {
	  margin-top : 0;
	}
  }
  &.accordion {
	.tab-list {
	  display : none;
	}
	.tab-panel {
		background-color : @background-color;
	}
	.tab-panel__title {
	  padding          : @item-padding;
	  color            : @item-text-color;
	  background-color : @item-color;
	  text-decoration  : none;
	  margin-bottom    : @item-margin-bottom;

	  &.visited {
		color:@item-text-color-visited;
		background-color: @item-color-visited;
		border-color: @item-border-color-visited;
	  }
	  &[aria-expanded="true"]{
		color: @item-text-color-selected;
		background-color: @item-color-selected;
		border-color: @item-border-color-selected;
	  }
	}

	.tab-panel__heading {
	  .item-title;
	  flex-grow   : 1;
	  font-family : @font-family;
	}
  }
}
