.thinking-mcq {
  .mcq-item {
    border-radius: 3px;
    label {
      color: @item-text-color;
      display: block;
      margin-bottom: @item-margin-bottom;
      position: relative;
      z-index: 1;
      cursor: pointer;
      background: @item-color;
      background: @secondary-gradient;
      background-size: 300% 300%;
      background-position: 90% 0;
      border: @global-border;
      border-radius: 2px;
      transition: all .6s cubic-bezier(.25, .8, .25, 1);

      .mcq-answer-icon {
        color: @item-text-color;

        &.checkbox {
          border: 3px solid @item-text-color;
        }
      }
    }
    input {
      &:checked + label {
        color: @item-text-color-selected;
        background-color: @item-color-selected;
        border-color: @item-border-color-selected;
        background-position : 50% 0;

        .mcq-answer-icon {
          &.radio {
            &:before {
              content: "\e65d";
            }
          }

          &.checkbox {
            border: 3px solid @item-text-color-selected;
            width: 18px;
            height: 18px;
            font-size: 18px;
            font-weight: bold;
            border-radius: 2px;

            &:before {
              content: "\e633";
            }
          }
        }

        .mcq-item-icon {
          color: @item-text-color-selected;
        }
      }

      &:disabled + label {
        color: @item-text-color-disabled;
        background-color: @item-color-disabled;
        border-color: @item-border-color-disabled;
        background-position: 90% 0;
        cursor: default;
      }

      &:checked:disabled + label {
        color: @item-text-color-selected;
        background-color: @item-color-selected;
        border-color: @item-border-color-selected;
        background-position: 50% 0;
        cursor: default;
      }

    }

    .mcq-item-inner {
      padding-top: @item-padding-top;
      padding-bottom: @item-padding-bottom;
      padding-right: @item-padding-right;
      padding-left: (@icon-size+(@item-padding-left*2));
    }

  }

  .question-card {

    .card-front,
    .card-back {

      .card-title {
        font-family : @font-family-headings;
        font-weight : @font-weight-black;
      }

      .card-footer {
        background-color : rgba(49, 117, 165, .1);
        border-top       : 1px solid rgba(49, 117, 165, .1);

        .btn {
          color           : #333;
          box-shadow      : none;
          background      : rgba(49, 117, 165, .05);
          transition      : all .6s cubic-bezier(.25, .8, .25, 1);
          text-decoration : none;
          border-radius   : 0;
          border          : none;

          &:hover, &:focus {
            background : rgba(49, 117, 165, .1);
          }
        }

        &[aria-hidden="true"] {
          border-top-color : transparent;
          .btn {
            display : none;
          }
        }
      }
    }

    .card-front {
      .card-footer {
        .btn {
          border-left : 1px solid rgba(49, 117, 165, .1);
        }
      }
    }

    .card-back {
      .card-footer {
        .btn {
          border-right : 1px solid rgba(49, 117, 165, .1);
        }
      }
    }
  }
}

.no-touch .thinking-mcq {
  .mcq-item label:hover {
    color: @item-text-color-hover;
    background-position: 60% 0;
    border-color: @global-border-color;

    .mcq-item-icon {
      color: @item-text-color-hover;
    }
  }

  .mcq-item input:disabled + label:hover {
    color: @item-text-color-disabled;
    background-color: @item-color-disabled;
    border-color: @item-border-color-disabled;
    background-position: 90% 0;
    cursor: default;
  }

  .mcq-item input:checked:disabled + label:hover {
    color: @item-text-color-selected;
    background-color: @item-color-selected;
    border-color: @item-border-color-selected;
    background-position : 50% 0;
    cursor: default;
  }
}
