.hotgraphic-component {

  .hotgraphic-graphic-pin {
    background-color : fade(@item-text-color, 5%);
    transition       : all .6s cubic-bezier(.25, .8, .25, 1);

    &:before,
    &:after {
      content       : "";
      box-sizing    : border-box;
      position      : absolute;
      top           : 0;
      left          : 0;
      border-radius : 50%;
      width         : 100%;
      height        : 100%;
      border        : 1px solid rgba(1, 49, 90, 1);
      opacity       : 0;
    }

    &:before {
      border-width : 3px;
    }

    .hotgraphic-graphic-pin-icon {
      transition : all .6s cubic-bezier(.25, .8, .25, 1);
    }

    &.visited {
      .hotgraphic-graphic-pin-icon {
        color : @item-color-visited;
      }
    }
    .no-touch &:hover, &:focus {
      background-color : fade(@item-text-color, 10%);

      &:before {
        animation-name            : pin-ripple;
        animation-duration        : 2s; /* or: Xms */
        animation-iteration-count : infinite;
        animation-timing-function : ease-out;
        animation-fill-mode       : forwards;
      }

      &:after {
        animation-name            : pin-ripple-short;
        animation-duration        : 2s; /* or: Xms */
        animation-iteration-count : infinite;
        animation-timing-function : ease-out;
        animation-fill-mode       : forwards;
      }

      .hotgraphic-graphic-pin-icon {
        color : @secondary-color;
      }
    }
  }

  .hotgraphic-graphic-pin-icon {
    color : @item-color;
  }

  .hotgraphic-popup {
    background-color : @background-color;
    border           : @item-border;
  }

  .hotgraphic-popup-toolbar {
    background-color : @item-color;
  }

  .hotgraphic-popup-nav {
    background-color : @item-color;
  }

  .hotgraphic-popup-done {
    .no-touch &:hover {
      .hotgraphic-popup-close {
        color : @button-text-color-hover;
      }
    }
  }

  .hotgraphic-popup-arrow-l,
  .hotgraphic-popup-arrow-r,
  .hotgraphic-popup-close {
    padding : @item-padding;
    color   : @button-text-color;
  }

  .hotgraphic-popup-count {
    padding : @item-padding;
    color   : @button-text-color;
  }

  .hotgraphic-popup-controls {
    &.disabled,
    &:disabled,
    &[aria-disabled="true"]
    &[disabled="disabled"]{
      visibility : hidden;
      background: transparent !important; //overrides default button style
      border: none;
      .hotgraphic-popup-arrow-l,
      .hotgraphic-popup-arrow-r {
        color : transparent !important; //overrides default button style
      }
    }

    .no-touch &:hover {
      .hotgraphic-popup-arrow-l,
      .hotgraphic-popup-arrow-r {
        color : @button-text-color-hover;
      }
    }
  }

  .hotgraphic-popup-inner {
    padding : @item-padding;
  }

  .hotgraphic-content-title {
    .item-title;
    margin-bottom : @title-text-margin;
  }
}

@keyframes pin-ripple-short {
  0% {
    opacity   : 0;
    transform : scale(1);
  }

  50% {
    opacity : .05;
  }
  100% {
    opacity   : 0;
    transform : scale(2.5);
  }
}

@keyframes pin-ripple {
  0% {
    opacity   : 0;
    transform : scale(1);
  }
  15% {
    opacity   : 0;
    transform : scale(1);
  }
  60% {
    opacity : .1;
  }
  100% {
    opacity   : 0;
    transform : scale(2.5);
  }
}