// Component styling
// =================

.component {
  .component-inner {
    .component-background;
    //border-bottom : 6px solid @primary-color;
    &.blank-inner { // don't apply styling to blank component
      background-color : transparent;
      padding          : 0;
      border           : 0;
    }
  }

  .component-title {
    display         : flex;
    flex-direction  : row-reverse;
    justify-content : flex-end;
    align-items     : flex-start;
    margin-bottom   : @component-title-margin-bottom;
    color           : @title-color;
    text-align      : left;

    .dir-rtl & {
      text-align : right;
    }
  }

  .component-title-inner {
    flex-grow : 1;
    padding   : 0;
  }

  .component-indicator {
    flex-grow        : 0;
    flex-shrink      : 0;
    height           : auto;
    width            : auto;
    font-size        : 12px;
    line-height      : 1;
    padding          : 4px 6px 3px;
    border-radius    : 50px;
    color            : @secondary-color-inverted;
    background-color : @secondary-color;
    border           : 2px solid @secondary-color;
    white-space      : nowrap;

    &:before {
      content       : "\2713\0020";
      position      : relative;
      top           : -1px;
      display       : inline-block;
      width         : 14px;
      height        : 14px;
      border-radius : 14px;
      border        : 1px solid @secondary-color-inverted;
      color         : @secondary-color-inverted;
      font-size     : 11px;
      line-height   : 14px;
      text-align    : center;
    }

    &:after {
      content     : "Complete";
      font-weight : 400;
    }

    &.incomplete {
      background-color : @background-color;
      color            : @foreground-color;

      &:before {
        content : "!";
        border  : 1px dotted @foreground-color;
        color   : @foreground-color;
      }

      &:after {
        content : "Required";
      }
    }

    .component-indicator-inner {
      display : none;
    }
  }

  .component-body {
    margin : @component-body-margin-top 0px @component-body-margin-bottom;

    a {
      text-decoration : underline;
      color           : @font-color;
    }
  }

  .component-body-inner {
    padding : 0px;
  }

  .component-instruction {
    margin-bottom : @component-instruction-margin-bottom;
    .instruction-text;
  }

  .component-instruction-inner {
    &.validation-error {
      color : @validation-error;
    }
  }

  // to replicate padding between blocks when components drop into a single column
  @media all and (max-width : (@device-width-small)) {

    &.component-left {
      padding : 0 0 (@block-padding / 3);
    }

    &.component-right {
      padding : (@block-padding / 3) 0 0;
    }
  }
}