// Drawer styling
// ==============

.drawer {
  background : @drawer-color;
  background : radial-gradient(circle at right, lighten(@drawer-color, 15%), @drawer-color 90%);
  color      : @drawer-item-text-color;

  .drawer-back {
    border : none !important;
  }

  .drawer-toolbar {
    position      : absolute;
    width         : 100%;
    top           : 0;
    border-bottom : @global-border;
  }

  .drawer-toolbar .icon {
    color   : @drawer-icon-color;
    padding : @navigation-padding;

    .no-touch &:hover {
      color : @drawer-icon-color-hover;
    }
  }

  .drawer-holder {
    padding-top : ((@navigation-padding * 2) + @icon-size + 1);
  }

  .drawer-inner {
    border-left : 5px solid @drawer-border-color;

    .dir-rtl & {
      border-left  : inherit;
      border-right : 5px solid @drawer-border-color;
    }
  }

  .drawer-item {
    .drawer-item-title {
      .sub-title;
    }

    .drawer-item-description {
      font-size   : 14px;
      line-height : @body-text-line-height;
    }

    .drawer-item-open {
      border-bottom       : @global-border;
      color               : @drawer-item-text-color;
      text-decoration     : none;
      width               : 100%;
      background-color    : transparent !important;
      background          : rgba(1, 49, 90, 0);
      background          : linear-gradient(165deg, rgba(1, 49, 90, .2), rgba(1, 49, 90, 0) 75%);
      background-size     : 400% 400%;
      background-position : 100% 100%;
      transition          : all .6s cubic-bezier(.25, .8, .25, 1);

      .no-touch &:hover {
        background-position : 20% 80%;
        color               : @drawer-item-text-color-hover;
      }
    }
  }

  .drawer-item-open {
    padding : @drawer-item-padding-top @drawer-item-padding-right @drawer-item-padding-bottom @drawer-item-padding-left;
    .transition-background-color;

    //stops rollover of drawar items on touch devices
    .no-touch &:hover {
      background-color : @drawer-color;
    }
  }
}
