// Notify styling
// ==============

.notify {
  .notify-popup {
    background-color : transparent;
    color            : @notify-text-color;
    padding          : 0 15px;
    box-sizing       : border-box;

    a {
      text-decoration : none;
    }
  }

  .notify-popup-inner {
    position   : relative;
    max-width  : @device-width-large;
    margin     : auto;
    padding    : 1px;
    box-shadow : 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
  }

  .notify-popup-content-inner {
    padding-left : 0;
  }

  .notify-popup-title {
    margin        : 15px 0 15px 30px;
    padding-right : 60px;

    @media (max-width : 520px) {
      font-size   : 24px;
      font-weight : 900;
      line-height : 120%;
    }
  }

  .notify-type-prompt {
    .notify-popup-body {
      padding-right : 60px;
    }
  }

  .notify-popup-body {
    color            : @background-color-inverted;
    background-color : @background-color;
    padding          : 15px 30px;
    a {
      text-decoration : underline;
    }
  }

  .notify-popup-done {
    padding          : 14px;
    margin           : 0;
    border-radius    : 0;
    border           : none;
    position         : absolute;
    top              : 0;
    right            : 0;
    background-color : rgba(255, 255, 255, 0);
    .transition-background-color;

    .notify-popup-icon-close {
      font-size : 32px;
      color     : @notify-icon-color;
    }
    .no-touch &:hover {
      background-color : rgba(255, 255, 255, 0.1);

      .notify-popup-icon-close {
        color : @notify-icon-color;
      }
    }
  }

  .notify-popup-icon-close {
    color : @notify-icon-color;

    .no-touch &:hover {
      color : @notify-icon-color-hover;
    }
  }

  .notify-popup-content-inner {
    .notify-popup-icon {
      position       : absolute;
      right          : 0;
      left           : inherit;
      padding-bottom : (@notify-padding-bottom / 3);

      .icon {
        font-size : 32px;
        height    : 32px;
        width     : 60px;
        color     : @notify-icon-color;
        margin    : 0;
      }
    }
  }

  .notify-popup-buttons {
    text-align       : center;
    padding          : 15px 30px 30px;
    background-color : @background-color;
  }

  .notify-popup-button {
    min-width : 100px;
  }

  .notify-shadow {
    background-color : @transparency;
  }
}

.notify-push {
  position         : fixed;
  right            : 20px;
  width            : 380px;
  background-color : @notify-color;
  transition       : all 0.3s;
  opacity          : 0;
  margin-top       : -30px;
  color            : #fff;
  z-index          : 50;
  border           : 1px solid @notify-text-color;
  border-radius    : 3px;

  .dir-rtl & {
    left  : 20px;
    right : inherit;
  }

  @media all and (max-width : @device-width-medium) {
    width     : 90%;
    max-width : 380px;
    right     : 5%;

    .dir-rtl & {
      left  : 5%;
      right : inherit;
    }
  }

  &.show {
    margin-top : 0px;
    opacity    : 1;
  }
}

.notify-push-inner {
  padding      : 30px 0px 30px 30px;
  margin-right : (30px + @icon-size*2);
  cursor       : pointer;

  .dir-rtl & {
    margin-left  : (30px + @icon-size*2);
    margin-right : inherit;
    padding      : 30px 30px 30px 0px;
  }
}

.notify-push-close {
  display         : inline-block;
  position        : absolute;
  right           : 30px;
  top             : 50%;
  margin-top      : -(@icon-size/2);
  text-decoration : none;

  .dir-rtl & {
    left  : 30px;
    right : inherit;
  }

  .icon {
    color : @notify-icon-color;

    .no-touch &:hover {
      color : @notify-icon-color-hover;
    }
  }
}

.notify-popup {
  .notify-popup-inner {
    background-color : @notify-color;
  }
  .notify-popup-title {
    color : @notify-text-color;
  }
  .notify-popup-body {
    color            : @background-color-inverted;
    background-color : @background-color;
  }
  .notify-popup-done {
    background-color : rgba(255, 255, 255, 0);

    .notify-popup-icon-close {
      color : @notify-icon-color;
    }
    .no-touch &:hover {
      background-color : rgba(255, 255, 255, 0.1);
      .notify-popup-icon-close {
        color : @notify-icon-color;
      }
    }
  }
  &.partially-correct,
  &.incorrect {
    .notify-popup-inner {
      background-color : @validation-error;
    }
    .notify-popup-title {
      color : @notify-text-color;
    }
    .notify-popup-body {
      color            : @background-color-inverted;
      background-color : @background-color;
    }
    .notify-popup-done {
      background-color : rgba(255, 255, 255, 0);

      .notify-popup-icon-close {
        color : @notify-icon-color;
      }
      .no-touch &:hover {
        background-color : rgba(255, 255, 255, 0.1);
        .notify-popup-icon-close {
          color : @notify-icon-color;
        }
      }
    }
  }

  &.correct {
    .notify-popup-inner {
      background-color : @validation-success;
    }
    .notify-popup-title {
      color : @notify-text-color;
    }
    .notify-popup-body {
      color            : @background-color-inverted;
      background-color : @background-color;
    }
    .notify-popup-done {
      background-color : rgba(255, 255, 255, 0);

      .notify-popup-icon-close {
        color : @notify-icon-color;
      }
      .no-touch &:hover {
        background-color : rgba(255, 255, 255, 0.1);
        .notify-popup-icon-close {
          color : @notify-icon-color;
        }
      }
    }
  }
}
