.qcard-flip {


  .card-front,
  .card-back {

    .card-footer {
      background-color : rgba(49, 117, 165, .1);
      border-top       : 1px solid rgba(49, 117, 165, .1);

      .btn {
        color           : #333;
        background      : rgba(49, 117, 165, .05);
        border-radius   : 0;
        border          : none;

        &:hover, &:focus {
          background : rgba(49, 117, 165, .1);
        }
      }
    }
  }

  .card-front {
    .card-footer {
      .btn {
        border-left : 1px solid rgba(49, 117, 165, .1);
      }
    }
  }

  .card-back {

    .card-footer {
      .btn {
        border-right : 1px solid rgba(49, 117, 165, .1);
      }
    }
  }
}