.parallax-inner {
  position              : relative;
  -webkit-box-sizing    : border-box;
  box-sizing            : border-box;
  display               : -webkit-box;
  display               : -ms-flexbox;
  display               : flex;
  -webkit-box-orient    : vertical;
  -webkit-box-direction : normal;
  -ms-flex-direction    : column;
  flex-direction        : column;
  -webkit-box-pack      : center;
  -ms-flex-pack         : center;
  justify-content       : center;
  -webkit-box-align     : center;
  -ms-flex-align        : center;
  align-items           : center;
  background-repeat     : no-repeat !important;
  background-size       : cover !important;

  &.parallax-fixed {
	background-attachment : fixed;
  }

  &.parallax-full {
	min-height : ~"calc(100vh - 96px)";
  }

  &.parallax-h-center-v-center {
	background-position : center;
  }

  &.parallax-h-center-v-top {
	background-position : center top;
  }

  &.parallax-h-center-v-bottom {
	background-position : center bottom;
  }

  &.parallax-h-left-v-center {
	background-position : left center;
  }

  &.parallax-h-left-v-top {
	background-position : left top;
  }

  &.parallax-h-center-v-bottom {
	background-position : left bottom;
  }

  &.parallax-h-right-v-center {
	background-position : right center;
  }

  &.parallax-h-right-v-top {
	background-position : right top;
  }

  &.parallax-h-right-v-bottom {
	background-position : right bottom;
  }

  .component-inner {
	-webkit-box-shadow : 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	box-shadow         : 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  }

  &.parallax-transparent {
	.component .component-inner {
	  background-color : transparent !important;
	  box-shadow       : none !important;
	  padding-left     : 0;
	  padding-right    : 0;
	}
  }

  .block {
	background : transparent;
  }



  .parallax-caption {
	position : absolute;
	bottom   : 32px;
	left     : 0;
	right    : 0;

	& > .caption-inner {
	  max-width        : 80%;
	  margin           : 0 auto;
	  padding          : 1px 16px;
	  background-color : rgba(255, 255, 255, .95);
	}
  }
}

.parallax-shadowed {
  -webkit-box-shadow : inset 0 0 8px rgba(0, 0, 0, 0.4);
  box-shadow         : inset 0 0 8px rgba(0, 0, 0, 0.4);
}

.article {

  .parallax-bg-none {
	background-color : transparent;
  }

  .parallax-bg-background {
	background-color : @background-color;

	.article-body,
	.article-title,
	.article-instruction,
	.block-body,
	.block-title,
	&.parallax-transparent .component-title,
	&.parallax-transparent .component-body,
	&.parallax-transparent .component-instruction {
	  color : @background-color-inverted;
	}
  }

  .parallax-bg-primary {
	background-color : @primary-color;

	.article-body,
	.article-title,
	.article-instruction,
	.block-body,
	.block-title,
