	&.parallax-transparent .component-body,
	&.parallax-transparent .component-instruction {
	  color : @secondary-color-inverted;
	}
  }

  .parallax-bg-tertiary {
	background-color : @tertiary-color;

	.article-body,
	.article-title,
	.article-instruction,
	.block-body,
	.block-title,
	&.parallax-transparent .component-title,
	&.parallax-transparent .component-body,
	&.parallax-transparent .component-instruction  {
	  color : @tertiary-color-inverted;
	}
  }

  .parallax-bg-quaternary {
	background-color : @quaternary-color;

	.article-body,
	.article-title,
	.article-instruction,
	.block-body,
	.block-title,
