	  color : @quaternary-color-inverted;
	}
  }

  .parallax-bg-foreground {
	background-color : @foreground-color;

	.article-body,
	.article-title,
	.article-instruction,
	.block-body,
	.block-title,
	&.parallax-transparent .component-title,
	&.parallax-transparent .component-body,
	&.parallax-transparent .component-instruction {
	  color : @foreground-color-inverted;
	}
  }
}