.carousel {
  border-radius : 2px;

  .carousel-nav {
    background-image : none;
    background-color : rgba(49, 117, 165, .1);
    border-top       : 1px solid rgba(49, 117, 165, .1);

    & > .btn {
      color           : #333;
      background      : rgba(49, 117, 165, .05);
      border-radius : 0;
      box-shadow    : none;
      border-color  : transparent;

      &.btn-next-slide {
        border-left-color: rgba(49, 117, 165, .1)
      }
      &.btn-prev-slide {
        border-right-color: rgba(49, 117, 165, .1)
      }

      & > .icon {
        color : #333;
      }
      &:hover, &:focus {
        background : rgba(49, 117, 165, .1);

        & > .icon {
          color : #333;
        }
      }
    }
  }

  .carousel-progress .carousel-progress-indicator.selected{
    background: @quaternary-color;
  }

}