.panels-inner .panels-item {
  padding: 0 (@component-padding/2);
}

.panels-inner .panels-card-title{
  padding: 4px (@component-padding / 2);
}

.panels-inner .panels-item-body {
  padding: (@component-padding / 2);
}

.panels-inner .panels-item .panels-card {
  padding: ((@component-padding/2) + 18px) (@component-padding/2) (@component-padding/2);
}


.panels-inner .panels-item.visible .panels-card{
  -webkit-transform: translate(0, -(@component-padding/2));
  transform: translate(0, -(@component-padding/2));
}

.panels-inner .panels-item.visited .btn-panels-expand {
  background: lighten(desaturate(@secondary-color, 50%), 10%);
  background-image:none;
}
