.flexmenu-menu {
  background-color: transparent;
  max-width: none;
  margin: auto;
  box-shadow: none;

  .flex-menu {

    .menu-header{
      margin-bottom: 0 ;
      background-color: #fff;
      background-image:none;
    }

    .menu-header-inner {
      box-sizing: border-box;
      padding: 32px 16px 16px;
      text-align: center;
      border-bottom: 1px solid rgba(138, 229, 221, 0.1);
      background-color: #fff;
      margin: 0;
    }

    .menu-title {
      position: static;
      top: 0;
      left: 0;
      background-color: transparent;
      padding: 8px;
      box-shadow: none;
      color: @foreground-color !important;
    }

    .menu-item {
      margin: 0;
    }

    .menu-item-title {
      font-size: 18px;
    }

    .menu-list-item-button {
      padding: 6px 20px;
      background: @background-color;
      border-radius: 0;
      box-shadow:none;

      &:after {
        right: 8px;
        top: 50%;
        margin-top: -12px;
        color: fade(@secondary-color-inverted, 10%);
      }

    }



    .menu-item-inner {
      position: relative;
      max-width: none;
      margin: 0 0 0 -320px;
      padding: 0;
    }

    .menu-item-duration {
      position: static;
      right: 0;
      float: none;
    }

    .pagelevelprogress-indicator .pagelevelprogress-indicator-bar {
      background-color: @primary-color;
    }
  }

}
