.gotopage__liveregion,
.gotopage__buttons {
  position: relative;
}

.gotopage__popover {
  position: absolute;
  bottom: 12px;
  left: 0;
  width: 100%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);

  &:after {
    content: "";
    position: absolute;
    bottom: -14px;
    left: 20px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 13.9px 8px 0 8px;
    border-color: @button-color transparent transparent transparent;
  }
}

.gotopage__popover_close {
  position: absolute;
  top: -10px;
  right: -10px;
  z-index: 3;
  padding: 4px;
  border-radius: 50%;

  .icon {
    color: @item-text-color;
  }
}

.gotopage__popover__backing {
  position: relative;
  z-index: 1;
  background-color: @white;
}

.gotopage__popover__header {
}

.gotopage__popover__title {
  position: relative;
  z-index: 2;
  color: @primary-color-inverted;
  background-color: @primary-color;
  padding: 0 10px;
  text-align: center;

  & > span {
    position: relative;
    top: 20px;
  }
}

.gotopage__popover__curve-adjust {
  position:relative;
  z-index: 1;
  width:100%;
  padding-top: 12.5%;
}

.gotopage__popover__curve {
  position: absolute;
  top: 0 ;
  left: 0 ;
  display: block;
  width: 100%;
  height: 100%;
  fill: @primary-color;
}

.gotopage__popover__body {
  text-align: center;
  padding: 10px 10px 16px;
  overflow: hidden;
}

.gotopage__popover__footer {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  -webkit-box-pack: end;
  justify-content: flex-end;

  & > button {
    -webkit-box-flex: 1;
    flex-grow: 1;
    flex-shrink: 0;
    min-width: 50%;
  }
}
