.component-container {

  @media all and (min-width: (@device-width-medium + 1px)) {
    &[class*="grid"] {
      display: -webkit-box;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      flex-direction: row;
      flex-wrap: nowrap;

      &.align-top {
        -webkit-box-align: start;
        align-items: flex-start;
      }

      &.align-bottom {
        -webkit-box-align: end;
        align-items: flex-end;
      }

      &.align-center {
        -webkit-box-align: center;
        align-items: center;
      }

      &.align-stretch {
        -webkit-box-align: stretch;
        align-items: stretch;
      }

      & > .component {
        -webkit-box-flex: 0;
        flex-grow: 0;
        flex-shrink: 0;
      }
    }
  }

  &.grid-1-11 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 8.33333%;
      }

      & > .component.component-right {
        width: 91.66666%;
      }
    }
  }

  &.grid-2-10 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 16.66666%;
      }

      & > .component.component-right {
        width: 83.33333%;
      }
    }
  }

  &.grid-3-9 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 25%;
      }

      & > .component.component-right {
        width: 75%;
      }
    }
  }

  &.grid-4-8 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 33.33333%;
      }

      & > .component.component-right {
        width: 66.66666%;
      }
    }
  }

  &.grid-5-7 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 41.66666%;
      }

      & > .component.component-right {
        width: 58.33333%;
      }
    }
  }

  &.grid-6-6 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 50%;
      }

      & > .component.component-right {
        width: 50%;
      }
    }
  }

  &.grid-7-5 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 58.33333%;
      }

      & > .component.component-right {
        width: 41.66666%;
      }
    }
  }

  &.grid-8-4 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 66.66666%;
      }

      & > .component.component-right {
        width: 33.33333%;
      }
    }
  }

  &.grid-9-3 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 75%;
      }

      & > .component.component-right {
        width: 25%;
      }
    }
  }

  &.grid-10-2 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 83.33333%;
      }

      & > .component.component-right {
        width: 16.66666%;
      }
    }
  }

  &.grid-11-1 {
    @media all and (min-width: (@device-width-medium + 1px)) {
      & > .component.component-left {
        width: 91.66666%;
      }

      & > .component.component-right {
        width: 8.33333%;
      }
    }
  }

  &.hide-first .component.component-left,
  &.hide-second .component.component-right {
    @media all and (max-width: (@device-width-medium + 1px)) {
      display: none;
    }
  }
}
