.card-layout-container {
  border-radius : 2px;

  .card-front,
  .card-back {
    border                  : @global-border;
    border-radius           : 2px;

    .card-header {
      border-top-left-radius  : 2px;
      border-top-right-radius : 2px;
    }

    .card-title {
      font-family : @font-family-headings;
      font-weight : @font-weight-black;
      .item-title;
    }

    .card-footer {
      height           : 2.5rem;
      background-color : rgba(49, 117, 165, .1);
      border-top       : 1px solid rgba(49, 117, 165, .1);

      .btn {
        color           : #333;
        box-shadow      : none;
        background      : rgba(49, 117, 165, .05);
        transition      : all .6s cubic-bezier(.25, .8, .25, 1);
        border-radius   : 0;
        border          : none;

        &:hover, &:focus {
          background : rgba(49, 117, 165, .1);
        }
      }
    }
  }

  .card-front {
    .card-footer {
      .btn {
        border-left: 1px solid rgba(49, 117, 165, .1);
      }
    }
  }

  .card-back {
    .card-footer {
      .btn {
        border-right: 1px solid rgba(49, 117, 165, .1);
      }
    }
  }
}