.flowmenu-menu {
  .menu-item {
    .menu-item-inner {
      background : @background-color;
      background : linear-gradient(165deg, @background-color, #e6f3ff 75%);
      border     : @global-border;

      &.hovered {
        .page-level-progress-menu-item {
          opacity : 1;
          & > .page-level-progress-menu-item-indicator > .page-level-progress-menu-item-indicator-bar {
            transform : translate(0);
          }
        }
      }

      .menu-item-title {
        position  : relative;
        z-index   : 3;
        width     : auto;
        padding   : 0;
        margin    : 1rem 0 1rem 1rem;
        font-size : 2rem;

        .menu-item-title-inner {
          font-family      : @font-family-headings;
          background-color : fade(@background-color, 0%);
        }
      }

      .page-level-progress-menu-item {
        opacity : .9;
        .percent-label {
          color : #000;
        }

        &:before {
        }

        & > .page-level-progress-menu-item-indicator {
          border        : @global-border;
          border-radius : 2px;
          background    : fade(@background-color, 80%);

          & > .page-level-progress-menu-item-indicator-bar {
            background    : @menu-item-progress-fill-color;
            border-radius : 2px;
          }
        }
      }
    }
  }
}

@keyframes flowmenu {
  0% {
    transform : translate(0, 0);
  }

  50% {
    transform : translate(-64px, 0);
  }
  100% {
    transform : translate(0, 0);
  }
}