@mcq-text-hover : @tertiary-color;
@lighten-success-value : 5%;
@lighten-validation-error : 5%;

.gmcq-component {
  .gmcq-item {
    @media all and (max-width : @device-width-medium) {
      width : 100%;
      &.even, &.odd {
        label {
          margin-left  : 0;
          margin-right : 0;
        }
      }
    }
  }

  &.col-4 {
    .gmcq-item {
      width : 25%;

      &.item-1, &.item-2 {
        label {
          margin-left  : 2.5%;
          margin-right : 2.5%;
        }
      }

      @media all and (max-width : @device-width-medium) {
        width : 100%;
        &.even, &.odd {
          label {
            margin-left  : 0%;
            margin-right : 0%;
          }
        }
      }
    }
  }

  .gmcq-item label {
    color            : @item-text-color;
    border           : none;
    background-color : transparent;

    .gmcq-answer-icon {
      color : @item-text-color;

      &.checkbox {
      }
    }

    &.selected {
      color            : @item-text-color-selected;
      background-color : transparent;

      .gmcq-item-icon {
        color : @item-text-color-selected;

        &.checkbox {
          border : 3px solid #333;
        }
        &.radio {
          border : 3px solid #333;
        }

        &.gmcq-correct-icon {
          color : lighten(@validation-success, @lighten-success-value);
        }

        &.gmcq-incorrect-icon {
          color : lighten(@validation-error, @lighten-validation-error);
        }
      }

      .gmcq-item-inner {
        color : @item-text-color-selected;
      }
    }

    &.disabled {
      .gmcq-item-icon {
        color : @item-color-disabled;
      }
      .gmcq-item-inner {
        color : @item-color-disabled;
      }
    }
  }

  .gmcq-item-inner {
    margin-left    : @icon-size * 2;
    color          : @item-text-color;
    padding-bottom : @item-padding-bottom;
    padding-top    : @item-padding-top;
    padding-left   : 0;
    padding-right  : 0;
    .dir-rtl & {
      margin-left  : inherit;
      margin-right : @icon-size * 2;
    }
  }

  .gmcq-item input {
    filter  : alpha(opacity=0); // ie8 fix for opacity on input
    opacity : 0;
  }

  .gmcq-correct-icon {
    color : lighten(@validation-success, @lighten-success-value);
  }

  .gmcq-incorrect-icon {
    color : lighten(@validation-error, @lighten-validation-error);
  }

  .gmcq-widget {
    &.show-user-answer {
      label.selected {
        .gmcq-item-inner {
          color : @item-text-color-selected;
        }
      }
    }

    &.show-correct-answer {
      label.selected {
        .gmcq-item-inner {
          color : @item-text-color-selected;
        }
      }
    }

    &.disabled {
      .gmcq-item label.selected.disabled {
        background-color : transparent;
        .gmcq-item-icon {
          &.gmcq-correct-icon {
            color : lighten(@validation-success, @lighten-success-value);
          }
          &.gmcq-incorrect-icon {
            color : lighten(@validation-error, @lighten-validation-error);
          }
        }
      }

      .gmcq-item label.disabled {
        color            : @item-color-disabled;
        background-color : transparent;
        border-color     : @item-border-color-disabled;
        border           : none;
      }
    }
  }
}

.no-touch {
  .gmcq-component {
    .gmcq-widget {
      &:not(.disabled) {
        .gmcq-item label:hover {
          color            : @mcq-text-hover;
          background-color : transparent;
          .gmcq-item-icon {
            color : @mcq-text-hover;
          }
          .gmcq-item-inner {
            color : @item-text-color-hover;
          }
        }
      }
    }
  }
}