.mcq-component {
  .mcq-item {
    position         : relative;
    background-color : transparent;
  }

  .mcq-item label {
    color               : @item-text-color;
    display             : block;
    margin-bottom       : @item-margin-bottom;
    position            : relative;
    z-index             : 1;
    cursor              : pointer;
    background          : @item-color;
    background          : @secondary-gradient;
    background-size     : 300% 300%;
    background-position : 90% 0;
    border              : @global-border;
    border-radius       : 2px;
    transition          : all .6s cubic-bezier(.25, .8, .25, 1);

    &.selected {
      color               : @item-text-color-selected;
      background-position : 50% 0;
      border-color        : @global-border-color;

      .mcq-answer-icon {
        color : @item-text-color-selected;

        &.radio {
          &:before {
            content : "\e65d";
          }
        }
        &.checkbox {
          &:before {
            content : "\e633";
          }
          border        : 3px solid @item-text-color-selected;
          width         : 18px;
          height        : 18px;
          font-size     : 18px;
          font-weight   : bold;
          border-radius : 2px;
        }
      }
      .mcq-item-icon {
        color : @item-text-color-selected;

        &.mcq-correct-icon {
          color : @validation-success;
        }

        &.mcq-incorrect-icon {
          color : @validation-error;
        }
      }
    }
  }

  .mcq-item-inner {
    padding-top    : @item-padding-top;
    padding-bottom : @item-padding-bottom;
    padding-right  : @item-padding-right;
    padding-left   : (@icon-size+(@item-padding-left*2));
  }

  .mcq-correct-icon {
    display : none;
  }

  .mcq-incorrect-icon {
    display : none;
  }

  .mcq-item-state {
    background : none;
    position   : absolute;
    left       : @item-padding-left;
    .dir-rtl & {
      left  : inherit;
      right : @item-padding-left;
    }
    width      : @icon-size;
    height     : @icon-size;
    top        : 50%;
    margin-top : -(@icon-size/2);
    z-index    : 0;
  }
}

.mcq-widget {

  &.disabled {
    .mcq-item label.selected.disabled {
      color            : @item-text-color-selected;
      background-color : @item-color-selected;
      border-color     : @item-border-color-selected;
      cursor           : default;
    }
    .mcq-item label.disabled {
      color            : @item-text-color-disabled;
      background-color : @item-color-disabled;
      border-color     : @item-border-color-disabled;
      cursor           : default;

      .mcq-item-icon {
        &.mcq-correct-icon {
          color : @validation-success;
        }
        &.mcq-incorrect-icon {
          color : @validation-error;
        }
      }
    }
  }
}

.no-touch {

  .mcq-widget {
    &:not(.disabled) {
      .mcq-item label:hover {
        color               : @item-text-color-hover;
        background-position : 50% 0;
        border-color        : @global-border-color;

        .mcq-item-icon {
          color : none;
        }
      }
      .mcq-item label.disabled {
        color            : @item-text-color-disabled;
        background-color : @item-color-disabled;
        border-color     : @item-border-color-disabled;
        &:hover {
          color            : @item-text-color-disabled;
          background-color : @item-color-disabled;
          border-color     : @item-border-color-disabled;
        }
        .mcq-item-icon {
          &.mcq-correct-icon {
            color : @validation-success;
          }
          &.mcq-incorrect-icon {
            color : @validation-error;
          }
        }
      }
    }
  }
}